import React from "react";
import styled from "styled-components"

export const AdUnitType = {
    Grid: 0,
    InArticle: 1
};

export default function AdUnit({ type = AdUnitType.Grid }) {
    const clientId = 'ca-pub-4889352773674825';

    React.useEffect(_ => {
        let p = { google_ad_client: clientId };
        if (typeof window === 'object') {
            (window.adsbygoogle = window.adsbygoogle || []).push(p);
        }
    }, []);

    if (type === AdUnitType.InArticle) {
        return <Ins className="adsbygoogle"
            //data-ad-test="on"
            data-ad-layout="in-article"
            data-ad-format="fluid"
            data-ad-client="ca-pub-4889352773674825"
            data-ad-slot="2581564797"></Ins>;
        // return <AdSense.Google
        //     client={clientId}
        //     slot='2581564797'
        //     style={{ width: '100%', height: '100%', minWidth: '250px' }}
        //     layout='in-article'
        //     format='fluid'
        // />;
    }

    return <Ins className="adsbygoogle"
        //data-ad-test="on"
        data-ad-client="ca-pub-4889352773674825"
        data-ad-slot="3654107349"
        data-ad-format="auto"
        data-full-width-responsive="true"></Ins>;
    // return <AdSense.Google
    //     client={clientId}
    //     slot='3654107349'
    //     style={{ width: '100%', height: '100%', minHeight: '250px'}}
    //     format='auto'
    //     //responsive='true'
    // />
}

const Ins = styled.ins`
display: block;
width: 100%;
height: 100%;
background: white;
box-shadow: 1px 1px 3px 0 rgba(0,0,0, 0.15);
`;